
  
<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      app
    >
      <v-list dense
      v-for="(menus, i) in menu"
            :key="i"
            menus
      >
        <v-list-item link
        :to="menus.url"
        >
          <v-list-item-content>
            <v-list-item-title>
             {{menus.title}}
             </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      app
      color="indigo"
      dark
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>Application</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col class="text-center">
            <v-tooltip left>
              <template v-slot:activator="{ on }">
                <v-btn
                  :href="source"
                  icon
                  large
                  target="_blank"
                  v-on="on"
                >
                  <v-icon large>mdi-code-tags</v-icon>
                </v-btn>
              </template>
              <span>Source</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer
      color="indigo"
      app
    >
      <span class="white--text">&copy; 2019</span>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    props: {
      source: String,
    },
    data: () => ({
      drawer: null,
      menu:[
      {title:'Главная страница', url:'/'},
      {title:'Вход', url:'/login'},
      {title:'Выход', url:'/loguot'},
      {title:'Регистрация', url:'/registr'},
      {title:'Мой профиль', url:'/myProfil'},
      {title:'Мой список', url:'/myAnimeList'},
      {title:'Список аниме', url:'/animeList'},
    ]
    }),
  }
</script>
